<template>
  <div class="myOrder">
    <!-- <van-pull-refresh v-model="isLoading" :disabled='pageNum > pages' @refresh="onRefresh"> -->
    <div class="noData" v-if="dataFlag">
      <img src="../assets/images/empty.png" alt="">
      <p>暂无数据~</p>
    </div>
    <div class="orderList" v-for="(item, i) in orderData" :key="i" v-else>
      <router-link :to="`/orderDetail/${item.orderId}`">
        <div class="card-title">
          <img :src="item.productPicture" alt="">
          <p>{{item.storeName}}</p>
        </div>
        <van-card :desc="`时长：${item.productDuration}个月`" :title="item.productName" :thumb="item.productPicture" :price="(item.orderPrice/100).toFixed(2)">

          <div slot="footer">
            <div class="footer__text">
              <span :class="{success:item.orderStatus=='000000',fail:item.orderStatus!='000000'}">{{item.orderStatus=='000000'?'续费中':item.orderStatus=='100100'?'订单取消':item.orderStatus=='100200'?'待支付首付':'续费失败'}}</span>
              <span>{{item.orderStatus=='000000'?(item.monthlyMortgage?`本期自动续费${item.monthlyMortgage}元`:''):item.orderStatus=='100100'?'':item.orderStatus=='100200'?'您的自动续费VIP学习开通未通过':'续费失败'}}</span>
            </div>
          </div>
        </van-card>
      </router-link>
    </div>

    <!-- </van-pull-refresh> -->

  </div>

</template>

<script>
import { queryOrderLIst } from '@/api/index.js';

export default {
  name: 'myOrder',
  props: ['id'],
  data() {
    return {
      pageNum: 1,
      pageSize: 50,
      orderData: [],
      hasData: 0,
      isLoading: false,
      pages: 1,
      dataFlag: false,
      loadFlag: false,
    };
  },
  mounted() {
    this.initData();
  },
  computed: {},
  methods: {
    onRefresh() {
      if (this.pageNum > this.pages) {
        return;
      }
      this.pageNum += 1;
      this.initData(this.pageNum, this.pageSize);
      setTimeout(() => {
        this.$toast('刷新成功');
        this.isLoading = false;
      }, 500);
    },
    initData() {
      queryOrderLIst(this.pageNum, this.pageSize)
        .then((result) => {
          if (result && result.list.length) {
            result.list.forEach((element) => {
              this.orderData.push(element);
            });
            this.pages = result.pages;
          } else {
            this.dataFlag = !this.orderData.length;
          }
          this.isLoading = false;
        })
        .catch(err => console.error(err));
    },
  },
  components: {},
};
</script>
